@import 'src/style/variable.scss';

.userblock {
  margin-left: calc(50%);
  transform: translateX(-50%);
  max-width: 600px;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;

  &__title {
    text-align: left;
    margin-bottom: 20px;

    p {
      font-weight: bold;
      color: var(--itemtext);
    }
  }

  &__wrap {
    width: 100%;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-direction: column;
  }
  &__row {
    width: 100%;
  }
  &__item {
    align-items: center;
    display: flex;
    justify-content: flex-start;
  }
  &__value {
    padding-right: 10px;
  }
  &__items {
    padding: 20px 20px;
    width: 100%;
    display: grid;
    grid-template: 1fr/ 1fr auto;
    border-bottom: 1px solid var(--sortBlock);
    p {
      color: var(--searchBlock);
    }
  }

  &__icon {
    width: 16px;
    height: 16px;
    overflow: hidden;
    cursor: pointer;
    svg {
      width: 100%;
      height: 100%;
      fill: var(--searchBlock);
      opacity: 0.8;
      &:hover {
        opacity: 1;
      }
    }
  }

  &__key {
    p:first-child {
      user-select: none;
    }
  }

  &__btn {
    height: 40px;
    width: 100px;
    margin-left: 20px;
    background: var(--searchBlock);
    p {
      color: var(--poblick) !important;
    }
  }
  &__cancel {
    height: 40px;
    width: 100px;
    border: 1px solid var(--searchBlock);
    display: flex;
    align-items: center;
    justify-content: center;
    margin-left: 20px;
    cursor: pointer;
    p {
      color: var(--searchBlock) !important;
    }
  }

  &__form {
    display: grid;
    grid-template: 1fr / 1fr auto;
    padding: 20px;
    border-bottom: 1px solid var(--sortBlock);
  }

  &__footer {
    display: flex;
  }

  @media (max-width: $mw2 + px) {
    & {
      position: static;
      padding-top: 35px;
      transform: none;
      margin-left: auto;
      margin-right: auto;
    }
  }
  @media (max-width: $mw5 + px) {
    &__items {
      grid-template: 1fr 1fr/ 1fr;
    }
    &__form {
      margin-bottom: 30px;
    }

    &__key {
      width: auto;
    }
    &__item {
      width: 100%;
    }
  }
  @media (max-width: $mw5 + px) {
    &__form {
      display: flex;
      margin-bottom: 20px;
      flex-direction: column;
    }
    //  &__items {
    //    margin-bottom: 20px;
    //  }
    &__footer {
      padding-top: 10px;
    }
    &__btn {
      margin-left: 0px;
    }
  }
}
