@import 'src/style/variable.scss';

.textareaitem {
  max-width: 500px;
  width: 100%;
  display: flex;
  flex-direction: column;

  &__input {
    width: 100%;
    display: flex;
    justify-content: center;
    padding-left: 20px;
  }

  textarea {
    border: 1px solid var(--sortBlock);
    color: var(--itemtext) !important;
    background: var(--poblick);
    max-width: 600px;
    width: 100%;
    height: 100px;
    border-radius: 2px;
    padding: 10px;
    outline: none;
  }

  @media (max-width: $mw4 + px) {
    &__input {
      padding-left: 0px;
    }
  }

  @media (max-width: 400px) {
    &__title {
      height: 35px;
    }
  }
}
