@import 'src/style/variable.scss';

.user {
  width: 100%;

  &__wrap {
    width: 100%;
    position: relative;
    height: 100%;
    min-height: 500px;
    height: 100%;
  }

  &__body {
    height: 100%;
  }
}

.useraside {
  position: absolute;
  left: 0;
  max-width: 200px;
  width: 100%;
  height: 100%;
  &__wrapper {
    height: 100%;
    display: grid;
    align-content: start;
    grid-template-rows: auto 1fr;
  }

  &__setting {
    width: 100%;
  }

  &__blocksecond {
    display: none;
    width: 25px;
    height: 25px;
    overflow: hidden;
    cursor: pointer;
    svg {
      width: 100%;
      height: 100%;
      fill: var(--itemtext);
    }
  }
}

.exit {
  padding-top: 20px;
  align-self: end;
  display: flex;
  justify-content: start;
  color: var(--itemtext);
  &__wrap {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: auto;
    cursor: pointer;
  }
  &__text {
    padding-right: 10px;
  }
  &__img {
    width: 15px;
    height: 15px;
    svg {
      width: 100%;
      height: 100%;
      fill: var(--itemtext);
    }
  }
}

@media (max-width: $mw2 + px) {
  .useraside {
    position: static;
    max-width: 100%;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 50px;
    &__blockfirst {
      display: none;
    }
    &__wrapper {
      width: 100%;
      height: 100%;
      grid-template-rows: 1fr;
      align-items: center;
      grid-template-columns: 1fr auto;
    }
    &__blocksecond {
      display: block;
    }
  }
  .exit {
    padding: 0;
    align-self: center;
    &__wrap {
      width: 100%;
      p {
        font-size: 16px;
      }
    }
    &__text {
      padding-right: 10px;
    }
  }
}
