@import 'src/style/variable.scss';

.select {
  position: relative;
  width: 100%;
  user-select: none;
  cursor: pointer;
  color: var(--itemtext);
  &.sort {
    display: none;
  }
  &__head {
    height: 100%;
    width: 100%;
    height: 35px;
    align-items: center;
    display: grid;
    grid-template: 1fr/ 1fr 20px;
    position: relative;
    & p {
      pointer-events: none;
      letter-spacing: 1px;
    }
  }

  &__blockharmonic {
    z-index: 1;
    transition: max-height 0.5s ease-in-out 0s;
    padding: 0;
    max-height: 0;
    height: auto;
    overflow: hidden;
    width: 100%;
    background: transparent;
  }
  &__blockemersion {
    display: flex;
    flex-direction: column;
    max-height: 600px;
  }

  &__select {
    height: 0;
    width: 0;
    overflow: hidden;
    select {
      visibility: hidden;
      opacity: 0;
    }
  }

  &.add &__blockharmonic {
    max-height: 600px;
  }

  &__blockemersion > li {
    position: relative;
    color: var(--itemtext);
    margin-bottom: 0px;
    height: 40px;
    cursor: pointer;
    display: grid;
    grid-template: 1fr / 1fr auto;
    align-items: center;
    width: 100%;

    p {
      line-height: 130% !important;
    }
  }

  &__addnew {
    display: flex !important;
    align-items: center;
  }

  &__check {
    display: grid;
    grid-template: 1fr / auto 1fr 1fr;
    align-items: center;
  }

  &__delete {
    justify-self: end;

    overflow: hidden;
    margin-left: 10px;
    cursor: pointer;
    display: flex;
    width: 15px;
    svg {
      width: 100%;
      height: 100%;
      fill: var(--itemtext);
    }
  }

  &__color {
    justify-self: start;
    width: 15px;
    height: 15px;
    border: 1px solid var(--searchBlock);
    position: relative;
  }

  &__text {
    min-width: 80px;
    padding-left: 10px;
  }

  &__plus {
    width: 15px;
    height: 15px;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  &__ok {
    width: 15px;
    height: 15px;
    border: 1px solid var(--searchBlock);
    position: relative;
    &:after {
      position: absolute;
      content: '';
      top: 6.5px;
      left: 1px;
      width: 5px;
      height: 1.5px;
      background: var(--searchBlock);
      transform: rotate(40deg);
      border-radius: 10px;
      visibility: hidden;
      opacity: 0;
    }
    &:before {
      position: absolute;
      content: '';
      top: 5.5px;
      right: 1px;
      width: 8.5px;
      height: 1.5px;
      background: var(--searchBlock);
      transform: rotate(-45deg);
      border-radius: 10px;
      visibility: hidden;
      opacity: 0;
    }
  }

  li.add &__ok {
    &:after {
      visibility: visible;
      opacity: 1;
    }
    &:before {
      visibility: visible;
      opacity: 1;
    }
  }

  &__arrow {
    transition: transform 0.2s ease 0s;
    transform: rotate(0deg);
    display: flex;
    justify-content: flex-end;
    align-items: center;
    justify-self: flex-end;
  }
  &.add &__arrow {
    transform: rotate(90deg);
  }

  & {
    position: relative;
  }
  &.sort {
    display: block;
  }
  &:before {
    content: '';
    position: absolute;
    left: 0;
    bottom: -1px;
    width: 100%;
    height: 1px;
    background-color: var(--sortBlock);
  }
  &__head {
    &:before,
    &:after {
      display: none;
    }
  }
  &__head,
  &__blockemersion {
    padding: 0 20px;
  }

  &__add {
    display: grid;
    grid-template: 1fr / 1fr 80px 46px;

    align-items: center;
  }
  &__inputtext {
    height: 35px;
    display: flex;
    align-items: center;
    input {
      font-size: 16px;
      color: var(--itemtext);
      outline: none;
      padding: 5px 0px;
      background: var(--poblick);
    }
  }
  &__btn {
    justify-self: end;
    grid-column: 3/4;
    display: flex;
    p:last-child {
      opacity: 0;
      transition: all 0.5s ease 0s;
    }
    &:hover,
    &:active {
      p:last-child {
        opacity: 1;
        padding-left: 5px;
      }
    }
  }
  &__inputcolor {
    padding: 0 20px;
    input {
      outline: none;
    }
  }

  @media (max-width: $mw2 + px) {
    &__head,
    &__blockemersion {
      padding: 0 20px;
    }

    &__check {
      grid-template: 1fr / auto 1fr auto;
    }
    &__delete {
      justify-self: end;
      width: 14px;
      overflow: hidden;
      margin-left: 15px;
      cursor: pointer;
      display: flex;
    }
  }
}

.selecthead {
  &__title {
    height: 40px;
    display: flex;
    align-items: center;
  }
  &:first-child {
    padding: 0;
  }
  &:last-child {
    padding-right: 0px;
  }
  &__title h3 {
    color: var(--searchBlock);
    pointer-events: none;
  }
  @media (max-width: $mw2 + px) {
    &__title {
      height: 35px;
    }
  }
}
