@import 'src/style/variable.scss';

.goodslist {
  display: flex;
  max-width: 800px;
  width: 100%;
  margin-left: calc(50%);
  transform: translateX(-50%);
  height: 100%;

  p {
    color: var(--itemtext);
  }

  &__wrap {
    width: 100%;
  }

  &__items {
    width: 100%;
  }

  &__turn {
    display: grid;
    grid-template: 1fr / 20px repeat(4, 1fr) 50px;
    justify-items: center;
    align-items: center;
    margin-bottom: 20px;
    padding: 0 10px;
  }
  &__type {
    grid-column: 2/3;
  }
  &__type:nth-child(2) {
    grid-column: 3/4;
  }
  &__type:nth-child(3) {
    grid-column: 4/5;
  }
  &__type:nth-child(4) {
    grid-column: 5/6;
  }
  @media (max-width: $mw1 + px) {
    & {
      max-width: 600px;
    }
  }
  @media (max-width: $mw2 + px) {
    & {
      position: static;
      padding-top: 20px;
      transform: none;
      margin-left: auto;
      margin-right: auto;
      max-width: 100%;
    }
  }
  @media (max-width: $mw4 + px) {
    &__turn {
      grid-template: 1fr / 20px repeat(4, 1fr);
    }
  }
  @media (max-width: 400px) {
    &__type:nth-child(4) {
      display: none;
    }
    &__turn {
      grid-template: 1fr / 10px repeat(3, 1fr);
    }
  }
  @media (max-width: 350px) {
    &__turn {
      grid-template: 1fr / 10px repeat(3, 1fr);
      padding: 0 5px;
    }
    //  &__type:nth-child(1) {
    //    display: none;
    //  }
    //  &__type:nth-child(2) {
    //    grid-column: 2/3;
    //  }
    //  &__type:nth-child(3) {
    //    grid-column: 3/4;
    //  }
  }
}

.goodsitem {
  width: 100%;
  padding: 0 10px;
  border-bottom: 1px solid var(--poblick);
  &.notchange {
    background: var(--gray);
  }
  &__wrap {
    display: grid;
    grid-template: 45px / 20px repeat(4, 1fr) 35px 15px;
    justify-items: center;
    align-items: center;
  }

  &__type {
    display: flex;
  }

  .changetype {
    margin-right: 20px;
    grid-column: 6/7;
  }

  .deletetype {
    grid-column: 7/8;
  }

  &__svg {
    width: 15px;
    overflow: hidden;
    cursor: pointer;
    a {
      width: 100%;
      height: 100%;
    }
    svg {
      width: 100%;
      height: 100%;
      fill: var(--itemtext);
    }
  }

  @media (max-width: $mw4 + px) {
    &__wrap {
      grid-template: 40px / 20px repeat(4, 1fr);
    }
    .changetype,
    .deletetype {
      display: none;
    }
  }
  @media (max-width: 400px) {
    &__type:nth-child(5) {
      display: none;
    }
    &__wrap {
      grid-template: 40px / 10px repeat(3, 1fr);
    }
    @media (max-width: 350px) {
      & {
        padding: 0 5px;
        p {
          font-size: 10px;
        }
      }
      &__wrap {
        grid-template: 40px / 10px repeat(3, 1fr);
      }
    }
  }
}
