@import 'src/style/variable.scss';

.pricedevice {
  max-width: 500px;
  width: 100%;
  display: flex;
  &__block {
    overflow: hidden;

    display: flex;
    align-items: flex-start;
    flex-direction: column;
  }

  label {
    margin-left: 20px;
    display: flex;
    align-items: center;
    p {
      display: block;
      color: var(--itemtext);
      padding: 0 10px;
      opacity: 0.8;

      &.price {
        border-right: 1px solid var(--sortBlock);
      }

      &.sale {
        border-left: 1px solid var(--sortBlock);
      }
    }
  }

  &__body {
    border-radius: 2px;
    max-width: 100px;
    width: 100%;
    width: auto;

    background: var(--poblick);
    border: 1px solid var(--sortBlock);
    display: flex;
    align-items: center;
  }

  &__block input {
    color: var(--itemtext);
    box-sizing: border-box;
    height: 40px;
    padding-left: 5px;
    background: transparent !important;
    font-weight: 500;
    font-size: 16px;
    border: none;
    padding: 0 10px;
  }
  @media (max-width: 400px) {
    &__text {
      height: 35px;
    }
    &__block input {
      width: 50px;
      font-size: 14px;
      height: 30px;
    }
  }
}
