@import 'src/style/variable.scss';

.changepass {
  display: flex;
  max-width: 500px;
  width: 100%;
  margin-left: calc(50%);
  transform: translateX(-50%);
  height: 100%;

  &__wrap {
    width: 100%;
  }

  &__btn {
    height: 40px;
    width: 100px;
    background: var(--searchBlock);
    p {
      color: var(--poblick) !important;
    }
  }
  &__footer {
    display: flex;
    justify-content: flex-end;
  }
  @media (max-width: $mw2 + px) {
    & {
      padding-top: 20px;
      transform: none;
      margin-left: auto;
      margin-right: auto;
    }
  }
}
