@import 'src/style/variable.scss';

.imgfiles {
  max-width: 500px;
  width: 100%;

  &__head {
    width: 100%;
  }

  &__body {
    margin-left: 20px;
  }

  &__svg {
    width: 15px;
    display: flex;
    align-items: center;
    justify-content: center;
    svg {
      width: 100%;
      height: 100%;
      fill: var(--searchBlock);
      opacity: 0.8;
    }
  }

  &__input {
    position: relative;
    width: 100%;
    height: 30px;
    display: flex;
    align-items: center;

    &:hover {
      svg {
        opacity: 1 !important;
      }
    }
  }

  label {
    position: absolute;
    left: 0;
    border: 1px solid var(--sortBlock);
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    padding: 0 5px;
    p {
      color: var(--itemtext);
      padding-right: 10px;
      line-height: 130%;
    }
  }

  input {
    cursor: pointer;
    opacity: 0;
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
  }

  @media (max-width: $mw2 + px) {
  }
  @media (max-width: $mw4 + px) {
    &__body {
      margin-left: 0px;
    }

    &__input {
      height: 40px;
    }
  }
  @media (max-width: 400px) {
  }
}

.itemfile {
  width: 100%;
  align-items: center;
  margin-top: 20px;
  display: grid;
  grid-template: 1fr / 2fr 1fr auto;
  p {
    color: var(--itemtext);
  }

  &__back {
    background: var(--goodsBack);
    margin-left: 20px;
    max-width: 55px;
    height: 55px;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 5px;
  }

  &__item {
    overflow: hidden;
    max-width: 40px;
    height: 40px;
    width: 100%;
    background-repeat: no-repeat;
    background-size: contain;
    background-position: center;
  }
  &__delete {
    width: 15px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    svg {
      width: 100%;
      height: 100%;
      fill: var(--itemtext);
    }
  }
}
