@import 'src/style/variable.scss';

.changegoods {
  max-width: 800px;
  width: 100%;
  margin-left: calc(50%);
  transform: translateX(-50%);
  .titleproductadmin {
    margin-top: 15px;
    height: 40px;
    display: flex;
    align-items: center;
    color: var(--itemtext);
    margin-bottom: 10px;

    &.block {
      margin-bottom: 2px;
    }
  }

  &__name {
    width: 100%;
    display: flex;
    justify-content: center;
    input {
      border-bottom: 1px solid var(--sortBlock) !important;
      color: var(--itemtext);
      background: transparent;
      padding: 0 20px;
      height: 40px;
      font-size: 16px;
    }

    input::placeholder {
      color: var(--itemtext) !important;
      font-size: 16px !important;
    }
  }

  &__nameinput {
    //  padding-left: 20px;
  }

  &__nameblock {
    max-width: 500px;
    width: 100%;
  }

  &__form {
    display: flex;
    width: 100%;
    align-items: center;
    flex-direction: column;
  }

  &__footer {
    padding-top: 50px;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: flex-end;
  }

  &__numbers {
    max-width: 500px;
    width: 100%;
    display: flex;
  }

  &__imageblock {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  &__btn {
    height: 40px;
    width: 100px;
    margin-left: 20px;
    background: var(--searchBlock);

    p {
      color: var(--poblick) !important;
    }
  }

  &__delete {
    height: 40px;
    width: 100px;
    margin-left: 20px;
    border: 1px solid var(--searchBlock);
    background: var(--poblick);
    p {
      color: var(--searchBlock) !important;
    }
  }

  &__select {
    max-width: 500px;
    width: 100%;
  }
  @media (max-width: $mw2 + px) {
    & {
      position: static;
      padding-top: 20px;
      transform: none;
      margin-left: auto;
      margin-right: auto;
    }
  }
  @media (max-width: $mw4 + px) {
    &__numbers {
      flex-direction: column;
    }
    .titleproductadmin {
      margin-top: 10px;
      margin-bottom: 5px;
    }
  }
  @media (max-width: $mw5 + px) {
    &__name {
      input::placeholder {
        font-size: 14px !important;
      }
    }
  }
  @media (max-width: 400px) {
  }
}
