@import 'src/style/variable.scss';

.harmonic {
  position: relative;
  width: 100%;
  user-select: none;
  cursor: pointer;
  color: var(--searchBlock);
  padding-bottom: 5px;
  &.sort {
    display: none;
  }
  &__head {
    height: 100%;
    width: 100%;
    height: 40px;
    align-items: center;
    display: grid;
    grid-template: 1fr/ 1fr 20px;
    position: relative;
    & p {
      pointer-events: none;
      letter-spacing: 1px;
    }
  }
  &__blockharmonic {
    z-index: 1;
    transition: max-height 0.5s linear 0s;
    padding: 0;
    max-height: 0;
    height: auto;
    overflow: hidden;
    width: 100%;
    background: transparent;
  }
  &__blockemersion {
    display: flex;
    flex-direction: column;
    max-height: 600px;
  }

  &.add &__blockharmonic {
    max-height: 600px;
  }

  &__blockemersion > li {
    color: var(--searchBlock);
    margin-bottom: 0px;
    height: 35px;
    cursor: pointer;
    padding-left: 10px;
    letter-spacing: 1px;
    width: 100%;

    transition: all 0.3s ease 0s;
    a {
      height: 100%;
      width: 100%;
      text-decoration: none;
      color: var(--searchBlock);
      display: flex;
      align-items: center;
      justify-content: flex-start;
    }
    p {
      opacity: 0.9;
      line-height: 130% !important;
      position: relative;
    }
    &:hover {
      padding-left: 15px;
      background: var(--blockitem);
    }

    &.add {
      padding-left: 15px;
      background: var(--blockitem);
    }
  }

  &__text {
    min-width: 80px;
    padding-left: 10px;
  }

  &__color {
    width: 15px;
    height: 15px;
    border: 1px solid var(--searchBlock);
    position: relative;
  }

  &__arrow {
    transition: transform 0.2s ease 0s;
    transform: rotate(0deg);
    display: flex;
    justify-content: flex-end;
    align-items: center;
    justify-self: flex-end;
  }
  &.add &__arrow {
    transform: rotate(90deg);
  }

  & {
    position: relative;
  }
  &.sort {
    display: block;
  }
  @media (max-width: $mw2 + px) {
    & {
      padding: 0;
    }
    &__head {
      height: 50px;
      padding: 0 20px;
      p {
        font-size: 18px;
      }
    }
    &__blockemersion > li {
      height: 50px;
      p {
        font-size: 16px;
      }
    }
    &__blockemersion {
      padding: 0 20px;
    }
    &:before {
      content: '';
      position: absolute;
      left: 0;
      bottom: -1px;
      width: 100%;
      height: 1px;
      background-color: var(--sortBlock);
    }
  }
  @media (max-width: $mw4 + px) {
    &__head {
      height: 45px;
      padding: 0 10px;
      p {
        font-size: 18px;
      }
    }
    &__blockemersion > li {
      height: 45px;
      p {
        font-size: 16px;
      }
    }
    &__blockemersion {
      padding: 0 10px;
    }
  }
}
